    padding: 5px 5px;
    justify-content: space-between;
    cursor: grab;
}
.MuiListItem-root:hover {
    background-color: lightgrey;
}
.MuiTypography-inherit {
    margin-right: 10px;
    font-family: monospace;
    font-weight: 400;
    letter-spacing: 0.1rem;
    color: inherit;
    text-decoration: none;
}
